@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600);
.footer {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding-bottom: 30px;
  margin: 0;
  background-color: #eee;
  font-weight: 600;
  border-top: 1px solid rgb(255, 122, 255);
}

.footer-contact {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.footer-contact a {
  margin: 16px;
}

.footer-images {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
}
.footer-images img {
  height: 100px;
  margin: 0 20px;
}

.header {
  display: flex;
  max-width: 1024px;
  height: 100px;
  align-items: center;
  justify-content: space-between;
  font-weight: 600;
  letter-spacing: 0.1em;
  font-size: 14px;
  /* margin: 10px 20px; */
  margin: 10px auto;
}

ul {
  list-style: none;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
  display: flex;
}

li {
  padding: 5px;
  text-align: center;
}

li a {
  color: #999;
  text-decoration: none;
}

.logo {
  height: 100px;
}

.active {
  background-color: purple;
  border-radius: 5px;
  -webkit-transition: background-color 1s;
  transition: background-color 1s;
}
.active a {
  color: white;
  -webkit-transition: color 2s;
  transition: color 2s;
}

.not-active a:hover {
  color: black;
}

@media only screen and (max-width: 666px) {
  nav {
    visibility: hidden;
    overflow: hidden;
  }
}

.jumboimg {
  width: 100%;
  height: 100%;
  object-fit: cover;
  /* transform: scaleX(-1) scaleY(-1);
  z-index: 5; */
}
.jumbotron {
  height: 600px;
}
.jumbotext {
  text-align: center;
  color: white;
  text-shadow: -1px -1px 0 purple, 1px -1px 0 purple, -1px 1px 0 purple,
    1px 1px 0 purple;
}
.jumbotext h1 {
  font-size: 80px;
  margin: 10px;
}
.jumbotext h3 {
  margin: 10px;
  font-size: 40px;
}
header {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 600px;
  width: 100%;
}

@media only screen and (max-width: 800px) {
  .jumboimg {
    object-fit: scale-down;
  }
  .jumbotron {
    height: auto;
  }
  header {
    height: auto;
  }
  .jumbotext h1 {
    font-size: 80px;
  }
  .jumbotext h3 {
    font-size: 40px;
  }
}

@media only screen and (max-width: 600px) {
  .jumbotext h1 {
    font-size: 40px;
  }
  .jumbotext h3 {
    font-size: 20px;
  }
}

.home {
  height: 100%;
}
.headline {
  width: 50%;
  margin: 60px auto;
  font-weight: 100;
  letter-spacing: 0.5px;
  text-align: center;
  line-height: 1.5;
}
.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  max-width: 1000px;
  margin: 0 auto;
  font-weight: 400;
  text-align: justify;
  line-height: 1.4;
  letter-spacing: 0.1px;
}
.cards img {
  width: 270px;
  height: 240px;
  object-fit: cover;
  margin: 20px 0;
  border-radius: 8px;
}

.cards h2 {
  font-weight: 400;
  text-align: center;
}

.nosework {
  max-width: 270px;
  padding: 5px;
}
.obedience {
  max-width: 270px;
  padding: 5px;
}
.puppy {
  max-width: 270px;
  padding: 5px;
}
